<template>
    <el-container style="height: 100%">
        <el-aside ref="projectTree" width="260px" style="height: 100%; padding-right: 10px; border-right: 1px solid #bbb">
            <el-tree :data="this.projectList" node-key="id" default-expand-all :expand-on-click-node="false" @node-click="selectProject">
                <template #default="{ data }">
                    <span :style="{ padding: '0px', cursor: 'pointer', fontSize: '14px', marginTop: '2px', fontWeight: selectProjectList.indexOf(data.name) > -1 ? '900' : 'normal', color: selectProjectList.indexOf(data.name) > -1 ? '#409eff' : '#3c3d40' }">
                        {{ selectProjectList.indexOf(data.name) > -1 ? ' ' + data.name : data.name }}
                    </span>
                </template>
            </el-tree>
        </el-aside>
        <!--  -->
        <el-container v-if="pageMode == 'search'">
            <el-header style="height: 40px; margin-top: -40px">
                <el-affix :offset="50">
                    <el-row style="background-color: #ffffff; border: 1px solid #d1d3d4; border-radius: 10px; padding-left: 20px; padding-right: 20px; line-height: 40px">
                        <!-- <el-col :span="2" style="padding-left: 5px">
                            <el-select v-model="searchMode" placeholder="">
                                <el-option label="" value="all"></el-option>
                                <el-option label="" value="method"></el-option>
                                <el-option label="" value="class"></el-option>
                            </el-select>
                        </el-col> -->
                        <el-col :span="6" style="padding-left: 5px">
                            <el-input ref="searchName" v-model="searchName" placeholder="" class="input-with-select" @keydown="searchEnter" clearable />
                        </el-col>
                        <el-col :span="10" style="padding-left: 5px">
                            <el-input ref="searchText" v-model="searchText" placeholder="" class="input-with-select" @keydown="searchEnter" clearable />
                        </el-col>
                        <el-col :span="2" style="padding-left: 10px">
                            <el-tooltip placement="bottom" effect="light">
                                <template #content>
                                    <p>### </p>
                                    <p></p>
                                </template>
                                <el-switch ref="searchIsStruct" v-model="searchIsStruct" width="50" inline-prompt active-text="" inactive-text="" />
                            </el-tooltip>
                        </el-col>
                        <el-col :span="2" style="padding-left: 5px">
                            <el-tooltip placement="bottom" effect="light">
                                <template #content>
                                    <p></p>
                                    <p></p>
                                </template>
                                <el-switch ref="searchIsDeprecated" v-model="searchIsDeprecated" width="50" inline-prompt active-text="" inactive-text="" />
                            </el-tooltip>
                        </el-col>
                        <el-col :span="1"></el-col>
                        <el-col :span="1" style="padding-left: 5px">
                            <el-button ref="searchButton" type="success" @click="search()" style="height: 32px">
                                <el-icon><Search /></el-icon>
                            </el-button>
                        </el-col>
                        <el-col :span="1"></el-col>
                        <el-col :span="1">
                            <el-button type="danger" @click="showHelpTour()" link size="small"></el-button>
                        </el-col>
                    </el-row>
                </el-affix>
            </el-header>
            <el-main v-loading="loading" ref="metaBody">
                <el-row>
                    <el-col v-if="helpTour" :span="24">
                        <!-- =================================================================================================================== -->
                        <!--  -->
                        <!--  -->
                        <div style="padding: 20px; border: 2px solid #aaf; border-radius: 20px; margin: 20px 21px 20px 21px">
                            <el-row>
                                <el-col :span="2">
                                    <div style="background-color: lightgrey; border-radius: 0px; font-size: 14px; width: 60px; height: 32px; text-align: center; line-height: 32px">
                                        <span id="metaType" style="font-weight: bold; color: dodgerblue"></span>
                                    </div>
                                </el-col>
                                <el-col :span="16" style="line-height: 32px; text-align: left">
                                    <el-tag id="metaIsDeprecated" type="error" style="margin: 5px" effect="dark"></el-tag>
                                    <el-tag id="metaQualifier" type="success" style="margin: 5px" effect="dark">public static</el-tag>
                                    <span id="metaClassName" style="font-size: 14px; color: #333; cursor: pointer; font-weight: bold; color: blue">className</span>
                                    <span style="font-size: 14px; color: #333; font-weight: bold; color: blue; padding-left: 10px; padding-right: 10px">.</span>
                                    <span id="metaMethodName" style="font-size: 14px; color: #333; font-weight: bold">methodName</span>
                                    <CopyDocument id="metaNameCopy" style="width: 1em; height: 1em; margin-left: 10px; margin-right: 10px; cursor: pointer" />
                                    <el-rate id="metaHelpfulRate" v-model="CONST_FIVE" />
                                </el-col>
                                <el-col :span="6">
                                    <div id="metaProjectName" style="text-align: right; font-size: 14px; font-weight: bold">projectName</div>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="19" id="metaCommentInfo" style="margin-top: 10px; font-size: 14px"></el-col>
                                <el-col :span="5">
                                    <div style="float: right">
                                        <el-button id="metaExp" type="primary" size="small" round></el-button>
                                        <el-button id="metaLog" type="warning" size="small" round></el-button>
                                        <el-button id="metaCode" type="danger" size="small" round>Code</el-button>
                                    </div>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="24" style="margin: 5px; padding: 5px"></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="24" id="metaCommentScene" style="margin-left: 5px; padding-left: 5px; color: blue"> </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="10" id="metaParams" style="border: 2px dotted #aaa; border-radius: 10px; margin: 5px; padding: 10px; font-size: 14px">
                                    <p>
                                        
                                        <el-text type="primary">String</el-text>
                                        :
                                        <el-text type="warning"></el-text>
                                    </p>
                                    <p>
                                        
                                        <el-text type="primary">Date</el-text>
                                        :
                                        <el-text type="warning"></el-text>
                                    </p>
                                </el-col>
                                <el-col :span="1" style="margin: 5px; padding: 5px"></el-col>
                                <el-col :span="11" id="metaReturn" style="border: 2px dotted #aaa; border-radius: 10px; margin: 5px; padding: 10px; font-size: 14px">
                                    <p>
                                        
                                        <el-text type="primary">bool</el-text>
                                        :
                                        <el-text type="warning"></el-text>
                                    </p>
                                    <p>
                                        
                                        <el-text type="primary">IOException</el-text>
                                        :
                                        <el-text type="warning">IO</el-text>
                                    </p>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="24" id="metaCommentLimit" style="margin-left: 5px; padding-left: 5px; color: red"> </el-col>
                            </el-row>
                            <!--  -->
                            <el-row>
                                <el-col :span="24">
                                    <el-tag id="metaKeyword" type="warning" style="margin: 5px"></el-tag>
                                    <el-tag id="metaKeyword" type="warning" style="margin: 5px"></el-tag>
                                </el-col>
                            </el-row>
                            <el-row style="margin-top: 5px">
                                <el-col :span="12" style="text-align: left">
                                    <span id="metaCreateTime" style="color: grey; font-size: 12px">2024-01-01 09:00:00 </span>
                                </el-col>
                            </el-row>
                        </div>
                        <!-- =================================================================================================================== -->
                    </el-col>
                    <el-col v-else :span="24">
                        <!-- =================================================================================================================== -->
                        <!--  -->
                        <el-row v-for="item in javadocItem" :key="item">
                            <el-col :span="24">
                                <java-doc-item-card v-bind:data="item"></java-doc-item-card>
                            </el-col>
                        </el-row>
                        <el-backtop></el-backtop>
                        <!-- =================================================================================================================== -->
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </el-container>
    <el-tour v-model="helpTour">
        <el-tour-step :target="$refs.projectTree?.$el" title=" " placement="right">
            <div></div>
            <div></div>
        </el-tour-step>
        <el-tour-step :target="$refs.searchName?.$el" title=" " description="" />
        <el-tour-step :target="$refs.searchText?.$el" title=" " description="" placement="bottom-end" />
        <el-tour-step :target="$refs.searchIsStruct?.$el" title=" " placement="bottom-end">
            <p>###</p>
            <p></p>
        </el-tour-step>
        <el-tour-step :target="$refs.searchIsDeprecated?.$el" title=" " placement="bottom-end">
            <p> @Deprecated </p>
            <p></p>
        </el-tour-step>
        <el-tour-step :target="$refs.searchButton?.$el" title=" " description=" Enter" placement="bottom-end" />
        <el-tour-step target="#metaType" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaIsDeprecated" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaQualifier" title=" " description="publicprivatestatic " placement="bottom" />
        <el-tour-step target="#metaClassName" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaMethodName" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaNameCopy" title=" " description="className.methodName" placement="bottom" />
        <el-tour-step target="#metaHelpfulRate" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaProjectName" title=" " description="" placement="bottom-end" />
        <el-tour-step target="#metaCommentInfo" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaExp" title=" " description=" htmlmarkdown " placement="bottom-end" />
        <el-tour-step target="#metaLog" title=" " description="" placement="bottom-end" />
        <el-tour-step target="#metaCode" title=" " description="" placement="bottom-end" />
        <el-tour-step target="#metaCommentScene" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaParams" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaReturn" title="    " placement="bottom">
            <p> </p>
            <p> </p>
        </el-tour-step>
        <el-tour-step target="#metaCommentLimit" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaKeyword" title=" " description="" placement="bottom" />
        <el-tour-step target="#metaCreateTime" title=" " placement="bottom" style="width: 500px">
            <p></p>
        </el-tour-step>
        <el-tour-step :target="ref3?.$el" title=" " content-style="width:800">
            <p>~</p>
            <p></p>
            <p></p>
            <v-md-editor v-model="this.helpTourCommentExp" mode="preview" />
        </el-tour-step>
    </el-tour>
</template>

<script>
import { ElMessageBox, ElMessage } from 'element-plus';
import { Search, Share, Guide } from '@element-plus/icons';
import JavaDocItemCard from './JavaDocItemCard';
import request from '../../../utils/request.js';
import _ from 'lodash';
export default {
    data() {
        return {
            CONST_FIVE: 5,
            helpTourCommentExp: `
\`\`\` java
/**
 * 
 * # id
 * # 
 * # 
 *
 * @param iDataAdapter 
 * @param idList 
 * @return 
 */
public DataTable selectByIds(IDataAdapter iDataAdapter, List idList)
\`\`\`
            `,
            helpTour: false,
            loading: false,
            searchText: '',
            searchName: '',
            searchMode: 'all',
            searchIsStruct: true,
            searchIsDeprecated: false,
            javadocItem: [],
            pageMode: 'search',
            selectProjectList: [],
            projectList: [],
            viewData: {
                currentProjectId: '',
                currentVersion: '',
                currentPackageName: '',
                currentClassId: '',
                packageList: [],
                classList: [],
                methodList: []
            }
        };
    },
    components: { JavaDocItemCard, Search },
    mounted() {
        this.getProjectList();
        // this.search();
    },
    methods: {
        selectProject(data) {
            if (this.selectProjectList.indexOf(data.name) > -1) {
                this.selectProjectList = this.selectProjectList.filter(item => item !== data.name);
            } else {
                this.selectProjectList.push(data.name);
            }
        },
        searchEnter(e) {
            if (e.keyCode == 13) {
                this.search();
            }
        },
        search() {
            if (this.loading) return;
            this.loading = true;
            const timer = setTimeout(this.doSearch, 250);
        },
        doSearch() {
            console.log(' ' + this.searchMode + ' ' + this.searchText);
            request({
                url: '/openapi/javadoc/search',
                method: 'post',
                data: {
                    mode: this.searchMode,
                    name: this.searchName,
                    text: this.searchText,
                    projects: this.selectProjectList,
                    isStruct: this.searchIsStruct,
                    isDeprecated: this.searchIsDeprecated
                }
            }).then(res => {
                if (res.code == 0) {
                    // json
                    _(res.data).forEach(function (item) {
                        // meta
                        if (item.imports) item.imports = JSON.parse(item.imports);
                        if (item.params) item.params = JSON.parse(item.params);
                        if (item.throwses) item.throwses = JSON.parse(item.throwses);
                        if (item.commentLog) item.commentLog = JSON.parse(item.commentLog);
                        // 
                        if (item.javaDocClassLite && item.javaDocClassLite.commentLog) item.javaDocClassLite.commentLog = JSON.parse(item.javaDocClassLite.commentLog);
                    });

                    this.javadocItem = res.data;

                    ElMessage({
                        message: '',
                        type: 'success',
                        duration: 1 * 1000
                    });
                }
                this.loading = false;
            });
        },
        gotoPage(page) {
            if (page == 'preview') {
                let routeData = this.$router.resolve({
                    name: 'preview',
                    params: {}
                });
                window.open(routeData.path, '_blank');
            }
            if (page == 'javadoc') {
                let routeData = this.$router.resolve({
                    name: 'javadoc',
                    params: {}
                });
                window.open(routeData.path, '_blank');
            }
        },
        getProjectList() {
            request({
                url: '/openapi/javadoc/projectList',
                method: 'post',
                data: { p: 'n' }
            }).then(res => {
                if (res.code == 0) {
                    this.projectList = res.data;
                }
            });
        },
        getPackageList(projectId, version) {
            this.viewData.currentProjectId = projectId;
            this.viewData.currentVersion = version;
            request({
                url: '/openapi/javadoc/packageList',
                method: 'post',
                data: {
                    projectId: this.viewData.currentProjectId,
                    version: this.viewData.currentVersion
                }
            }).then(res => {
                if (res.code == 0) {
                    this.viewData.packageList = res.data;
                }
            });
        },
        showHelpTour() {
            this.helpTour = true;
        }
    }
};
</script>

<style>
.el-main {
    /* background-color: #e9eef3; */
    /* color: #333; */
    text-align: left;
}
.el-card {
    margin: 10px;
}
</style>
